@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
  --desktop: 1200px;
  --laptop: 1024px;
  --tablet: 768px;
  --mobile: 480px;
}

.navbarstyle {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 1%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.navright {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.mainHead {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 34px;
  padding-left: 10px;
  text-decoration: none;
  background-image: linear-gradient(to right, #fdc830, #f37335);
  /* background-size: 100%; */
  -webkit-background-clip: text;
          background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.mainHead:hover {
  background-image: linear-gradient(to right, #fc4a1a, #f7b733);
  /* background-size: 100%; */
  -webkit-background-clip: text;
          background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.navright>h4 {
  padding: 15px;
  font-family: Roboto, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  cursor: pointer;
}

.navright>h4>a {
  cursor: pointer;
  text-decoration: none;
  color: #756f6f;
}

.navright>h4>a:hover {
  background-image: linear-gradient(to right, #fdc830, #f37335);
  /* background-size: 100%; */
  -webkit-background-clip: text;
          background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.hideMe {}

@media only screen and (min-width:768px) {
  .hideMe {
    display: none;
  }
}

@media only screen and (max-width:768px) {
  .hideMe {
    display: none;
  }

  .mainHead {
    font-size: 24px;
  }

  .navright>h4 {
    font-size: 16px;
  }
}

@media only screen and (max-width:560px) {
  .hideMe {
    display: inline;
  }

  .mainHead {
    font-size: 14px;
  }

  .navbarstyle {
    display: none;
  }

  .navright {
    display: none;
  }
}
.App {
  text-align: center;
}

.compress {
  margin-bottom: 10px;
  align-content: center;
}

.maincon {
  margin-left: 20%;
  width: 80%;
  height: 80%;
}

input[type="file"] {
  display: none;
}

.custom-file-upload {
  display: inline-block;
  padding: 8px 15px;
  cursor: pointer;
  background-color: #FC8B06;
  font-weight: 500;
  color: white;
  border-radius: 20px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  
}

.text {
  margin-top: 2px;
  text-transform: capitalize;
  font-size: medium;
}

.Desktop {
  text-align: center;
}

.Mobile {
  text-align: center;
}

.head-title {
  font-weight: bold;
}


.rules-main {
  padding-bottom: 10px;
  font-family: Arial, Helvetica, sans-serif;
}

.rule-point {
  text-align: justify;
}

.icons {
  text-decoration: none;
  cursor: pointer;
}

.main-content {
  text-align: start;
  padding-left: 10px;
  margin-left: 2%;
  margin-bottom: 1%;
  margin-top: 2%;
  color: #353839;
}

.main-content-para {
  margin: 10px;
  width: 100%;
  margin-left: 5px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  align-items: Left;
  color: #353839;
  /* padding: 5px; */
}

.spanBold {
  font-weight: bold;
}

.blogsView {
  display: flex;
  flex-direction: row;
  padding: 20px;
  flex-wrap: wrap;
}

.mainContentStyle {
  margin-top: 20px;
  justify-content: space-between;

}

.cutWord {}

.displayMobile {}

@media only screen and (min-width:768px) {
  .displayMobile {
    display: none;
  }
}

@media only screen and (max-width:768px) {
  .main-content {
    width: 90%;
  }

  .main-content-para {
    font-size: smaller;
    text-align: justify;
  }

}

@media only screen and (max-width:480px) {
  .main-content-para {
    width: 100%;
    font-size: 12px;
    text-align: justify;
  }

  .cutWord {
    display: none;
  }
}
